.showcase {
  border-top: 1px solid var(--color-porcelain);
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.02)), color-stop(5%, #fff));
  background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0.02) 0%, #fff 5%);
  background: -o-linear-gradient(top, rgba(0, 0, 0, 0.02) 0%, #fff 5%);
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.02) 0%, #fff 5%);
}

.showcase-button {
  letter-spacing: -0.2px;
  padding: 2em 0 0.1em;
  margin: 0;
  text-align: center;
  font-size: var(--font-size__main);
  color: var(--color-lynch);
}

.showcase-legend {
  color: var(--color-silver);
  margin: 0.5em 0;
  text-align: center;
  line-height: 1.6;
  font-size: var(--font-size__medium);
  letter-spacing: 0.9px;
  font-weight: 300;
}

.showcase-button a {
  text-decoration: none;
  color: inherit;
}

.showcase-grid {
  padding: 0 2em;
  margin: 2em auto;
  max-width: 120em;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.showcase-item {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 15px;
  padding: 30px;
  width: calc((100% / 3) - 30px);
  background-color: #ffffff;
  border: 1px solid #e6e7eb;
  border-radius: 4px;
  -webkit-box-shadow: 0 3px 4px 0 rgba(75, 84, 94, 0.1);
          box-shadow: 0 3px 4px 0 rgba(75, 84, 94, 0.1);
}

.showcase-component {
  min-height: 260px;
}

.showcase-caption {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-top: 16px;
  margin-top: 16px;
  margin-bottom: -8px;
  border-top: 1px solid var(--color-porcelain);
}

.showcase-item h4 {
  margin: 0;
  margin-right: 0.7em;
  display: inline-block;
  width: 70%;
  color: var(--color-lynch);
  font-weight: normal;
  font-size: var(--font-size__base);
  letter-spacing: 0.3px;
}

.showcase-item a {
  text-decoration: none;
  display: block;
  color: var(--color-silver);
  font-size: var(--font-size__base);
  letter-spacing: 0.5px;
  margin-top: 1em;
}

.showcase-item a.source {
  float: right;
  margin-top: 0.2em;
}

.showcase-item__new {
  display: -ms-flexbox;
  display: flex;
}

.showcase-item__new a {
  margin: auto;
  text-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: 0 4px 3px rgba(0, 0, 0, 0.04);
          box-shadow: 0 4px 3px rgba(0, 0, 0, 0.04);
  position: relative;
  background: #eeeeee80;
  padding: 0.5em 1em;
  border-radius: 40px;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  color: var(--color-lynch);
}

.showcase-item__new a:before {
  content: "+";
  display: inline-block;
  margin-right: 0.5em;
}



